a {
  text-decoration: none; }
body, html {
  font-weight: 400;
  height: 100%;
  width: 100%; }
body {
  font-size: 16px;
  font-family: 'Roboto', sans-serif; }
*:focus {
  outline: 0; }
li {
  list-style: none; }
button {
  padding: 0;
  border: none;
  background: none; }
img {
  max-width: 100%;
  display: block; }
h1, h2, h3,h4, h5, ul, figure {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }
main {
  margin: auto;
  width: 100%; }
*:focus {
  outline: none; }
.pagination {
  width: 100%;
  text-align: center;
  margin: 2rem 0 rem(60) 0;

  & li {
    display: inline-block;
    vertical-align: middle; }
  & a {
    display: inline-block;
    vertical-align: middle;
    padding: .3rem .6rem !important;
    margin: 0 .15rem;
    border-radius: .25rem;
    color: white;
    background: #f6cd07; }
  & span {
    font-size: 1.3rem;
    font-weight: 400;
    display: inline-block;
    vertical-align: middle;
    padding: .3rem .6rem !important;
    margin: 0 .15rem;
    border-radius: .25rem;
    background: #EEE;
    color: #C1C1C1; } }


//Variables Generales

$greenDark: #27383F;
$greenLight: #C5D257;
$widthContent: 71.25rem;

.Button {
  line-height: 2.75rem;
  background: #C5D257;
  font-size: .85rem;
  font-weight: 500;
  width: 8rem;
  display: inline-block;
  text-align: center;
  color: #253A1B;
  border-radius: .125rem;
  &:hover {
    cursor: pointer;
    box-shadow: inset 0 -.375rem 0 #B1BD4E; } }
.AlignRight {
  text-align: right; }
.Title {
  padding: 1rem 0 2.5rem;
  font-weight: 500; }
.BackContainer {
  overflow: hidden;
  background: #ffffff;
  box-shadow: 0 0 rem(30) 0 rgba(0,0,0,0.02); }
.Map {
  width: 100%;
  height: rem(300);
  border-top: rem(6) solid rgba(0,0,0,0.5);
  border-bottom: rem(6) solid rgba(0,0,0,0.5); }


//Mensajes popup plataforma

.MessagePlatform {
  height: 100vh;
  width: 100vw;
  background: rgba(242, 242, 242, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20; }
.MessagePlatform-content {
  max-width: rem(780);
  height: auto;
  background: #FFFFFF;
  box-shadow: 0 0 rem(33) 0 #D4D4D4;
  padding: rem(64);
  position: relative;
  text-align: justify;
  p {
    font-weight: 300; }
  a {
    color: $greenDark;
    text-decoration: underline; }
  & h2 {
    color: $greenDark;
    font-weight: 100; } }
.MessagePlatform-last {
  text-align: right;
  span {
    color: $greenDark;
    font-weight: 300; } }
.MessagePlatform-close {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  background: #7F7F7F;
  font-size: rem(24) !important;
  color: $greenLight;
  font-weight: 100 !important;
  padding: rem(8) rem(12) rem(6) !important;
  cursor: pointer; }
.MessagePlatform-ok {
  border-left: rem(24) solid #E3F268; }
.MessagePlatform-warning {
  border-left: rem(24) solid #fff961; }
.MessagePlatform-error {
  border-left: rem(24) solid #ff5b65; }


