@import "lib/settings";
@import "lib/general";
@import "lib/grid";
@import "lib/font";
@import "theme/footer";
$widthContent: 60rem;
$orange : #C75809;
$yellow  : #F1D100;
$gray : rgb(109, 110, 113);

.Header {
  width: 100%;
  background: #fdd200;
  position: relative;
  figure {
    max-width: rem(180); } }
.Header-content {
  max-width: $widthContent;
  margin: auto;
  padding: rem(5) 0  rem(15) 0; }
main {
  max-width: $widthContent;
  margin: 2rem auto;
  position: relative; }
body {
  font-size: .875rem; }

.Nav-back {
  a {
    padding: rem(8);
    color: black;
    font-size: rem(16);
    font-weight: 500; } }
.Pay {
  h1 {
    font-size: rem(26);
    color: #6d6e71; }
  p {
    font-size: rem(16);
    color: #6d6e71; }
  .user {
    text-transform: capitalize; }
  button {
    padding: rem(10) rem(50);
    background: #67b9e5;
    margin: rem(20) 0;
    color: white;
    font-size: rem(14); } }

.Admin-h2 {
  font-size: 1.4rem;
  font-weight: 300;
  color: #6d6e71;
  b {
    font-weight: 400; } }
.Admin-table {
  margin: auto;
  width: 100%;
  max-width: rem(800);
  box-shadow: 0 rem(3) rem(10) #e3e3e3;
  thead {
    background: #fdd200; }
  th {
    border-left: rem(1) solid #f6cd07;
    &:first-child {
      border-left: none; } }
  th, td {
    padding: rem(16) rem(4);
    text-align: center; }
  tbody {
    tr:nth-child(even) {
      background: #fffbe8; } }
  svg {
    max-width: rem(25);
    &:hover g {
      fill: #be9e0b; } }
  .loader {
    max-width: rem(30);
    display: none; } }
.Admin-payUpdate {
  background: #f6cd07;
  padding: rem(8) rem(16) rem(7);
  color: black;
  font-weight: 500;
  &:hover {
    box-shadow: inset 0 rem(-4) 0 #e8c20c; } }

.Search {
  margin: rem(30) auto rem(8);
  max-width: rem(800);
  position: relative;
  input {
    width: rem(300);
    padding: rem(8) rem(4); }
  button {
    display: block;
    position: absolute;
    right: rem(3);
    top: rem(3);
    width: rem(30); } }
